  export default {
    name: 'mobiriseClone',
    props: ['title', 'content', 'image', 'container', 'header'],
    computed: {
      backgroundImage() {
        return `background-image: url(${this.image})`
      },
      headerStyle() {
        return this.header ? `header-${this.header}` : ''
      }
    }
  }
